.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.control-buttons {
    bottom: 0;
}

.btn-control {
    height: 5rem;
}

.time {
    line-height: 1;
    margin: 0;
    padding: 0 !important;
    font-size: 30vw;
}

.btn-timer {
    height: 4rem;
    width: 4rem;
    font-size: 2rem;
}
